import _ from 'lodash'
import api from 'api'
import { config } from 'librarys/lightcone_v3';
import { notification } from 'antd';
import { matchPath } from "react-router-dom";
import { push } from 'connected-react-router'

const initState = {
  markets: [],
  stars: [],
  tabs: [],
  currentTab: null,
  currentMarkets: [],
  currentMarket: null,
  cond: {
    sort: 'market',
    sortDirection: 'asc'
  }
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    updateFilterCond: (state, payload) => {
      let {cond, currentMarkets} = state
      cond = { ...cond, ...payload }
      currentMarkets = _.orderBy(currentMarkets, [cond.sort], [cond.sortDirection])
      return {
        ...state,
        cond,
        currentMarkets
      }
    },
    updateMarketsTicker: (state, tickers) => {
      let formatTicker = ticker => {
        let { bid, ask, open } = ticker
        let latest = (Number(bid) + Number(ask)) / 2
        open = Number(open)
        let variation = open ? (latest - open) / open : 0
        return { latest, variation }
      }

      let { currentMarkets, currentMarket, cond } = state
      // 
      // 
      currentMarkets = currentMarkets.map(item => {
        let ticker = _.find(tickers, {market: item.market})
        if (!ticker) {
          return item
        }

        let { latest, variation } = formatTicker(ticker)
        let { pricePrecision } = item
        item.latest = latest.toFixed(pricePrecision)
        item.variation = variation
        return item
      })
      currentMarkets = _.orderBy(currentMarkets, [cond.sort], [cond.sortDirection])

      // 
      if (currentMarket) {
        let currentMatched = _.find(tickers, {market: currentMarket.market})
        // current 
        if (currentMatched) {
          let { latest, variation } = formatTicker(currentMatched)
          let { pricePrecision } = currentMarket
          currentMarket.latest = latest.toFixed(pricePrecision)
          currentMarket.variation = variation
        }
      }

      return {
        ...state,
        currentMarkets,
        currentMarket,
      }
    }
  },
  effects: dispatch => ({
    async fetchMarkets (payload, { router }) {
      //  'LRC-ETH'
      let markets = await config.getMarkets()
      if (!markets) return

      // 
      const match = matchPath(router.location.pathname, {
        path: "/trade/:market",
        exact: true,
        strict: true
      })
      let { market = 'LRC-ETH' } = match.params
      let [ defaultBaseSymbol, defaultQuoteSymbol ] = market.split('-')
      
      let quoteTokens = _.chain(markets).map(market => market.quoteToken).uniq().value()
      let defaultTab = defaultQuoteSymbol
      let tabs = ['', ...quoteTokens]
      this.update({
        markets,
        tabs,
      })
      
      //  tab
      dispatch.markets.setCurrentTab(defaultTab)
      
      // 
      let currentMarkets = _.filter(markets, {quoteToken: defaultTab})
      let currentMarket = _.find(currentMarkets, { baseToken: defaultBaseSymbol })
      if (currentMarket) {
        dispatch.markets.setCurrentMarket(currentMarket)
      }
    },
    async setCurrentTab (tab, { markets }) {
      let currentMarkets = []
      if (tab === '') {
        currentMarkets = markets.stars
      } else {
        currentMarkets = _.filter(markets.markets, {quoteToken: tab})
      }

      this.update({
        currentTab: tab,
        currentMarkets,
      })

      //  setCurrentMarket
      // let currentMarket = currentMarkets[0]
      // if (currentMarket) {
      //   dispatch.markets.setCurrentMarket(currentMarket)
      // }
    },

    async startFetchMarketInfo (payload, { markets }) {
      let { currentMarket: { market } } = markets
      console.info(`====== ${market} ======`)

      dispatch.depth.fetchDepth()
      dispatch.trade.fetchTrade()
      dispatch.order.fetchOrders()
      // dispatch.socket.subscribe([
      //   // `candlestick&${market}&1Hour`,
      //   `depth&${market}&1`,
      //   `trade&${market}`,
      //   `ticker&${market}`
      // ])
    },
    async setCurrentMarket (currentMarket, { markets, socket }) {
      this.update({
        currentMarket,
      })
      dispatch.placeOrder.initialize()
      // 
      dispatch(push(`/trade/${currentMarket.market}`))

      if (!socket.ws || socket.ws.readyState !== 1) {
        if (socket.ws && socket.ws.readyState === 3) {
          // ( depth,trade,orders )
          // () 
          // socket 

          // TODO : socket
          // notification['success']({
          //   duration: 0,
          //   message: 'socket ',
          //   description: ''
          // })
          location.reload()
        }
        return
      }
      dispatch.markets.startFetchMarketInfo()
    },
    async onSocketReady (payload, { markets, depth }) {
      if (!markets.currentMarket) return
      dispatch.markets.startFetchMarketInfo()
    },

    async starMarket (currentMarket, { markets, depth }) {
      let stars = _.xorBy([currentMarket], markets.stars, 'market')
      this.update({
        stars,
      })
      // tab, 
      if (markets.currentTab === '') {
        dispatch.markets.setCurrentTab('')
      }
    },
    async fetchCurrentMarkets (payload, { markets, dexAccount }) {
      let { apiKey } = dexAccount
      let { currentMarkets, currentMarket } = markets
      if (!currentMarkets) return

      let pairs = currentMarkets.map(item => item.market)
      //  currentMarket  currentMarkets currentMarket 
      if (currentMarket) {
        pairs = _.uniq([...pairs, currentMarket.market])
      }
      if (!pairs.length) return

      let tickers = await api.getTickers({
        markets: pairs,
        apiKey
      })

      //  reducer tickerscurrentMarkets
      this.updateMarketsTicker(tickers)
    }
  })
}